import React, {Component} from 'react';
import ApiService from "./ApiService";

import '../css/layout.css';

class ChatComp extends Component{
	constructor(props){
		super(props);
		
        this.state={
			senderId:"bluc",
			message:null
		}	
	}
	
	componentDidMount(){
		this.sendMessage("hello");
	}
	
	handleKeyPress=(event)=>{

		if(event.charCode === 13){
			var data={
				senderId:"blue",
				message:event.target.value
			}
			
			ApiService.sendMsg(data)
				.then(res=>{
					console.log(res);
				})
		}
	}
	
	sendMessage=(msg)=>{
		console.log(msg);
	}
	
	render(){
		return(
            <div className="dimmed_layer_wrapper">
                <div className="full_layer">
                    <div className="common_alert"> 
	    				<div class="header">
	        				CHAT
					    </div>
					    <div class="chat">
					        <ul>
					        </ul>
					    </div>
					    <div class="input-div">
					        <textarea 
								placeholder="Press Enter for send message."
								name="message"
								onKeyPress={this.handleKeyPress}/>
					    </div>
	
					    <div class="chat format">
					        <ul>
					            <li>
					                <div class="sender">
					                    <span></span>
					                </div>
					                <div class="message">
					                    <span></span>
					                </div>
					            </li>
					        </ul>
                    	</div>
                	</div>
            	</div>
			</div>
		);
	}
}
export default ChatComp;
